/* time-bar
	----------------------------------- */
.time-bar {
	font-size: 0;
	text-align: center;
	&__btn {
		display: inline-block;
		vertical-align: middle;
		&:not(:first-child) {
			margin-left: 19px; }
		.icon-sun {
			font-size: 39px; }
		.icon-moon {
			font-size: 31px; } }
	&__btn.is-active &__icon {
		opacity: 1; }
	&__icon {
		fill: $white;
		opacity: .3; } }
